//Ï†ÑÎ∂Ä Ï≤¥ÌÅ¨
const termsAllChkBtn = document.getElementById('terms_all_chk');
//Ïù¥Ïö©ÏïΩÍ¥ÄÎèôÏùò Ï≤¥ÌÅ¨
