//Í∞úÏù∏Ï†ïÎ≥¥ ÏàòÏßë Ï≤¥ÌÅ¨
const termsChk02Btn = document.getElementById('terms_chk_02');
//Ï†ÑÏ≤¥ Ï≤¥ÌÅ¨Î•º Ï†úÏô∏Ìïú Ï≤¥ÌÅ¨Î≤ÑÌäº
const terms = document.querySelectorAll('.terms_chk');
