//1. Í≥µÎ∞±Ï≤¥ÌÅ¨ -> Í≥µÎ∞±ÏïÑÎãàÎ©¥ Ïù¥ÌõÑ Ï°∞Í±¥Îì§ Ï≤¥ÌÅ¨
function valueChecks() {
    const thisIndex = Array.from(necessaryInput).indexOf(this);
    const guideMessageArea = this.parentNode.lastElementChild;

    if (this.value === '' || this.value === undefined || this.value === null) {
        falseOn(this, guideMessageArea); //Ìï¥Îãπ Ïù∏Îç±Ïä§ ÏöîÏÜåÏùò Î∞∏Î•òÍ∞Ä Í≥µÎ∞±Ïù¥Î©¥ false Ï∂îÍ∞Ä Ìï®ÏàòÎ°ú
        guideMessageArea.textContent = 'ÏûÖÎ†•Í∞íÏùÑ ÏûÖÎ†• ÌòπÏùÄ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî!';
    } else {
        etcCheck(this, thisIndex);
    } 
}

//ÏïÑÏù¥Îîî confirm ÏÉÅÌÉú 
let idConfirm;
//Ï§ëÎ≥µÌôïÏù∏Ï≤¥ÌÅ¨ , ÏïÑÏù¥ÎîîÏùò Í≤ΩÏö∞ Î≤ÑÌäºÏùÑ ÌÜµÌï¥ Ï≤¥ÌÅ¨
userIdChkBtn.addEventListener('click', userIdCheck);
function userIdCheck() {
    const guideMessageArea = userId.parentNode.lastElementChild;
    const duplicateCheck = userData.some(object => object.userIDInfo === userId.value);

    if (userId.value === '' || userId.value === undefined || userId.value === null) {
        falseOn(userId, guideMessageArea);
        guideMessageArea.textContent = 'ÏûÖÎ†•Í∞íÏùÑ ÏûÖÎ†• ÌòπÏùÄ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî!';
    } else if (!(regexTest02.test(userId.value))) {
        falseOn(userId, guideMessageArea);
        guideMessageArea.textContent = 'Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ ÏûÖÎ†•ÏûÖÎãàÎã§. (ÌäπÏàòÎ¨∏Ïûê _Îäî ÌïúÎ≤àÎßå Í∞ÄÎä•, ÏòÅÏñ¥ ÏÜåÎ¨∏Ïûê ÎòêÎäî Ïà´Ïûê ÌäπÏàòÎ¨∏Ïûê_ Ï°∞Ìï© Í∞ÄÎä•, 6~15Ïûê)'
    } else if (duplicateCheck) {
        falseOn(userId, guideMessageArea);
        guideMessageArea.textContent = 'Ï§ëÎ≥µÎêú ÏïÑÏù¥ÎîîÏûÖÎãàÎã§!'
    } else {
        trueOn(userId, guideMessageArea);
        guideMessageArea.textContent = 'ÏïåÎßûÏùÄ ÏñëÏãùÏûÖÎãàÎã§!';
        //userIdÏ†ÑÎã¨
        idConfirm = true;
    }
}

